body {
  margin: 0px;
  padding: 0px;
  width: 100%;
  height: 100%;
}

nav {
  height: auto;
  width: 100%;
}

img.i1 {
  padding-top: 0px;
  padding-bottom: 3px;
  padding-right: 5px;
  height: 21px;
  width: 23px;
}

ul.U1 {
  padding-bottom: 5px;
  font-size: 22px;
}

ul.U2 {
  float: right;
}


label.Ap1 {
  padding-top: 20px;
  cursor: pointer;
}

label.Ap2 {
  position: relative;
  padding-top: 20px;
  padding-right: 20px;
  float: right;
  color: black;
  cursor: pointer;
}

label.Ap3 {
  cursor: pointer;
  color: black;
}

label.Ap4 {
  cursor: pointer;
  color: black;
}

label.Ap3:hover {
  color: rgb(43, 65, 189);
  cursor: pointer;
}

label.Ap4:hover {
  color: rgb(43, 65, 189);
  cursor: pointer;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (max-width: 950px) {
  nav {
    height: auto;
    width: 100%;
  }

  img.i1 {
    padding-top: 0px;
    padding-bottom: 3px;
    padding-right: 5px;
    height: 16px;
    width: 18px;
  }
}

#checkbtn {
  float: right;
  line-height: 80px;
  margin-top: -24px;
  margin-right: 12px;
  font-size: 30px;
  color: black;
  display: none;
  cursor: pointer;
}

#checkbtn1{
  display: none;
}

#check {
  display: none;
}


/* input [type="checkbtn"]:checked ~ label.Ap2 {
  left: 0;
} */

#checkbtn1:checked ~label.Ap2{
  left: 0;
}

@media (max-width: 500px) {
  label.Ap2 {
    padding-left: 0px;
  }
}

@media (max-width: 650px) {  
  label.Ap5 {
    display: none;
  }

  label.U2 {
    display: block;
    float: right;
    /* padding-left: 100px; */
  }

  label.Ap2 {
    text-decoration: none;
    display: block;
    position: absolute;
    border-radius: 7%;    
    text-decoration: none;
    background-color: rgb(207, 223, 236, 0.7);
    width: 100%;
    left: -700px;
    transition: 0.7s;
  }

  label.Ap4 {
    display: block;
    text-align: center;
  } 

  #checkbtn1{
    float: right;
    cursor: pointer;
    padding-top: 18px;
  }

  #checkbtn:hover{
    color:rgb(43, 65, 189);
  }

  #checkbtn {
    float: right;
    display: block;
    cursor: pointer;
    padding-top: 18px;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}